'use client'

import React, { useState } from 'react';
import Link from 'next/link';
import { ArrowUpRight, Thermometer, Droplets, Menu, X, ChevronUp, MapPin } from 'lucide-react';
import {
  getAirQualityColor,
  formatPMValue,
  getRecommendationIcon,
  useMonitoringData,
  useLocationMonitoringData
} from '../data/monitoring-data';

const Header = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  return (
    <header className="h-16 bg-green-100 border-b border-green-100 flex-shrink-0">
      <div className="h-full flex items-center justify-between px-4">
        <div className="flex items-center gap-4">
          <div className="w-18 h-18 flex-shrink-0 flex items-center justify-center">
            <img
              src="/assets/images/logo.png"
              alt="Mupcop logo"
              className="w-14 h-14 object-contain"
              width={82}
              height={82}
            />
          </div>
          <div>
            <h1 className="text-xl font-bold text-black">Mupcop</h1>
            <h2 className="text-xs font-light text-black max-w-xl">
              Multi-factor ultrafine particle optimization prediction
            </h2>
          </div>
        </div>

        <nav className="hidden sm:flex items-center gap-4">
          <Link href="/" className="px-3 py-1 text-base font-semibold text-black rounded-lg hover:bg-green-200 transition-colors">
            Air quality
          </Link>
          <Link href="/history-data" className="px-3 py-1 text-base font-semibold text-black rounded-lg hover:bg-green-200 transition-colors">
            History data
          </Link>
        </nav>

        <button
          className="sm:hidden"
          onClick={() => setIsMenuOpen(!isMenuOpen)}
        >
          {isMenuOpen ? <X size={20} /> : <Menu size={20} />}
        </button>
      </div>
    </header>
  );
};

const MonitoringPanel = ({ selectedLocation, onLocationClear }) => {
  // State สำหรับ popup
  const [showRecommendationPopup, setShowRecommendationPopup] = useState(false);
  const [selectedRecommendations, setSelectedRecommendations] = useState([]);

  // Debug การรับข้อมูล
  console.log('MonitoringPanel received selectedLocation:', selectedLocation);

  // ใช้ข้อมูลตามตำแหน่งที่เลือก หรือใช้ข้อมูลเริ่มต้น
  const defaultData = useMonitoringData();
  const locationData = useLocationMonitoringData(selectedLocation);

  console.log('Default data status:', !!defaultData);
  console.log('Location data status:', !!locationData);
  console.log('Selected location for monitoring:', selectedLocation);

  // เลือกข้อมูลที่จะแสดง
  const data = selectedLocation ? locationData : defaultData;
  console.log('Final data being used:', !!data, 'from', selectedLocation ? 'location' : 'default');

  const statusColor = getAirQualityColor(data.mainReading.status);

  // ฟังก์ชันสำหรับเปิด popup
  const handleRecommendationClick = () => {
    setSelectedRecommendations(data.recommendations);
    setShowRecommendationPopup(true);
  };

  // ฟังก์ชันสำหรับปิด popup
  const closePopup = () => {
    setShowRecommendationPopup(false);
    setSelectedRecommendations([]);
  };

  if (!data) return (
    <div className="flex items-center justify-center h-full">
      <div className="text-center">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-gray-800 mb-2"></div>
        <p className="text-sm text-gray-600">กำลังโหลดข้อมูล...</p>
      </div>
    </div>
  );

  return (
    <div className="w-full lg:w-1/3 bg-gray-50/50 border-r border-gray-100 overflow-auto relative">
      <div className="p-2 sm:p-3">
        {/* Location Header */}
        <div className="flex items-center justify-between mb-2 sm:mb-3">
          <h2 className="text-lg sm:text-xl font-light text-black-800">UFPs Monitoring</h2>
          {selectedLocation && onLocationClear && (
            <button
              onClick={onLocationClear}
              className="text-xs text-gray-500 hover:text-gray-700 bg-gray-100 hover:bg-gray-200 px-2 py-1 rounded-md transition-colors"
            >
              × ล้าง
            </button>
          )}
        </div>

        {/* Main reading card */}
        <div className="bg-white rounded-lg p-2 sm:p-3 shadow-sm border border-gray-100 mb-2 sm:mb-3">
          <div className="flex items-center justify-between mb-2">
            <div className="flex items-center gap-2">
              <div className="w-2 h-2 bg-green-500 rounded-full animate-pulse" />
              <span className="text-sm sm:text-base font-semibold text-black">LIVE</span>
            </div>
            <span className="text-xs text-black">{data.date}</span>
          </div>

          {/* Location name in main reading */}
          <div className="text-xs text-gray-600 mb-2 sm:mb-3">
            <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-1 sm:gap-0">
              <div className="flex items-center gap-1">
                <MapPin className="w-3 h-3" />
                <span className="truncate">
                  {selectedLocation ? selectedLocation.name : "Cafe Amazon สาขา ST"}
                </span>
              </div>
              <div className="text-xs text-gray-500 sm:ml-2">
                อัพเดท: {data.time}
              </div>
            </div>
          </div>

          {/* PM Reading Section - ปรับสำหรับมือถือ */}
          <div className="flex rounded-lg mb-2 sm:mb-3 shadow-sm overflow-hidden">
            {/* ส่วนซ้าย: ค่า PM และหน่วย */}
            <div
              className="w-2/5 sm:w-2/5 p-2 sm:p-4 flex flex-col justify-center relative"
              style={{ backgroundColor: statusColor }}
            >
              {/* เพิ่ม overlay เข้มเพื่อให้ข้อความขาวชัด */}
              <div className="absolute inset-0 bg-black bg-opacity-30"></div>
              <div className="relative z-10">
                <div className="text-xs font-bold mb-1 text-white text-shadow-lg">
                  {data.mainReading.type || 'PM0.1'}
                </div>
                <div className="flex items-baseline mb-1">
                  <span className="text-2xl sm:text-3xl font-black text-white" style={{ textShadow: '2px 2px 4px rgba(0,0,0,0.8)' }}>
                    {data.mainReading.value}
                  </span>
                </div>
                <div className="text-xs font-semibold text-white" style={{ textShadow: '1px 1px 2px rgba(0,0,0,0.8)' }}>
                  ({data.mainReading.unit})
                </div>
              </div>
            </div>

            {/* ส่วนขวา: สถานะและหมายเหตุ */}
            <div
              className="w-3/5 sm:w-3/5 p-2 sm:p-4 text-white flex flex-col justify-center text-center"
              style={{ backgroundColor: statusColor }}
            >
              <div className="text-lg sm:text-xl font-bold">{data.mainReading.status}</div>
            </div>
          </div>

          {/* Temperature and Humidity - ปรับสำหรับมือถือ */}
          <div className="flex justify-center gap-6 sm:gap-12 text-xs mb-2 sm:mb-3 py-2 sm:py-3 bg-gray-50 rounded-lg">
            {/* Temperature */}
            <div className="flex items-center gap-2 sm:gap-3">
              <img
                src="/assets/images/temperature.png"
                alt="Temperature"
                className="w-7 h-7 sm:w-8 sm:h-8"
                onError={(e) => {
                  e.target.style.display = 'none';
                  e.target.nextSibling.style.display = 'inline';
                }}
              />
              <Thermometer className="w-4 h-4 text-gray-500" style={{ display: 'none' }} />
              <span className="text-gray-700 font-medium text-xs sm:text-sm">{data.conditions.temperature}</span>
            </div>

            {/* Humidity */}
            <div className="flex items-center gap-2 sm:gap-3">
              <img
                src="/assets/images/humidity.png"
                alt="Humidity"
                className="w-7 h-7 sm:w-8 sm:h-8"
                onError={(e) => {
                  e.target.style.display = 'none';
                  e.target.nextSibling.style.display = 'inline';
                }}
              />
              <Droplets className="w-4 h-4 text-gray-500" style={{ display: 'none' }} />
              <span className="text-gray-700 font-medium text-xs sm:text-sm">{data.conditions.humidity}</span>
            </div>
          </div>

          {/* Recommendations Icons */}
          <div className="border-t border-gray-100 pt-2 sm:pt-3">
            <div className="text-center">
              <button
                onClick={handleRecommendationClick}
                className="flex justify-center items-center gap-6 sm:gap-10 hover:bg-gray-50 p-2 rounded-lg transition-colors w-full flex-wrap"
                title="คลิกเพื่อดูคำแนะนำทั้งหมด"
              >
                {data.recommendations.slice(0, 5).map((recommendation, index) => {
                  const iconPath = getRecommendationIcon(recommendation);
                  const isImageIcon = typeof iconPath === 'string' && iconPath.startsWith('/assets/images/');

                  return (
                    <div key={index} className="flex flex-col items-center">
                      {isImageIcon ? (
                        <img
                          src={iconPath}
                          alt=""
                          className="w-6 h-6 sm:w-8 sm:h-8"
                          onError={(e) => {
                            e.target.style.display = 'none';
                            e.target.nextSibling.style.display = 'inline';
                          }}
                        />
                      ) : (
                        <span className="text-base sm:text-lg">{iconPath}</span>
                      )}
                      {isImageIcon && (
                        <span style={{ display: 'none' }} className="text-base sm:text-lg">•</span>
                      )}
                    </div>
                  );
                })}
              </button>
              {data.recommendations.length > 5 && (
                <div className="text-xs text-gray-500 mt-1">
                  และอีก {data.recommendations.length - 5} ข้อ - คลิกเพื่อดูทั้งหมด
                </div>
              )}
            </div>
          </div>
        </div>

        {/* PM Summary Card - แก้ไขแล้ว */}
        <div className="bg-white rounded-lg p-2 sm:p-3 shadow-sm border border-gray-100 mb-2 sm:mb-3">
          <h3 className="text-sm font-medium text-gray-800 mb-2 sm:mb-3">ข้อมูลมลพิษทางอากาศ</h3>
          <div className="grid grid-cols-3 gap-1 sm:gap-2">
            {data.pmReadings && data.pmReadings.length > 0 ? (
              data.pmReadings.map((reading, index) => {
                // ตรวจสอบว่า reading มีข้อมูลครบ
                if (!reading || !reading.type || reading.value === undefined) {
                  return null;
                }

                // แยกค่าตัวเลขจาก string หรือรับตัวเลขตรง ๆ
                let numericValue;
                if (typeof reading.value === 'number') {
                  numericValue = reading.value;
                } else {
                  numericValue = parseFloat(reading.value.toString().replace(/[^\d.-]/g, ''));
                }

                // ตรวจสอบว่าค่าที่ได้เป็น number ที่ถูกต้อง
                if (isNaN(numericValue)) {
                  return null;
                }

                // กำหนดสถานะตาม PM type และค่า
                let status = 'Good';
                if (reading.type === 'PM0.1') {
                  if (numericValue <= 0.0) status = 'Good';
                  else if (numericValue <= 0.5) status = 'Warning for sensitive groups';
                  else if (numericValue <= 1.0) status = 'Warning';
                  else if (numericValue <= 2.0) status = 'Effects on health';
                  else status = 'Hazardous';
                } else if (reading.type === 'PM2.5') {
                  if (numericValue <= 15.0) status = 'Good';
                  else if (numericValue <= 25.0) status = 'Warning for sensitive groups';
                  else if (numericValue <= 37.5) status = 'Warning';
                  else if (numericValue <= 75.0) status = 'Effects on health';
                  else status = 'Hazardous';
                } else if (reading.type === 'PM10') {
                  if (numericValue <= 50.0) status = 'Good';
                  else if (numericValue <= 80.0) status = 'Warning for sensitive groups';
                  else if (numericValue <= 120.0) status = 'Warning';
                  else if (numericValue <= 180.0) status = 'Effects on health';
                  else status = 'Hazardous';
                }

                const bgColor = getAirQualityColor(status);

                return (
                  <div
                    key={`${reading.type}-${selectedLocation?.id || 'default'}-${index}`}
                    className="rounded-lg p-1.5 sm:p-2 text-white text-center"
                    style={{ backgroundColor: bgColor }}
                  >
                    <div className="text-xs font-medium mb-1">{reading.type}</div>
                    <div className="text-xs sm:text-sm font-bold">{numericValue.toFixed(2)}</div>
                    <div className="text-xs opacity-90">μg/m³</div>
                  </div>
                );
              })
            ) : (
              // Fallback ถ้าไม่มีข้อมูล PM readings
              <div className="col-span-3 text-center text-xs text-gray-500 py-4">
                ไม่มีข้อมouลมลพิษในขณะนี้
              </div>
            )}
          </div>
        </div>

        {/* Click instruction */}
        <div className="mt-2 p-2 bg-yellow-50 border border-yellow-200 rounded-lg">
          <div className="text-xs text-yellow-800">
            💡 <strong>วิธีใช้:</strong> คลิกที่หมุดบนแผนที่เพื่อดูข้อมูลคุณภาพอากาศของตำแหน่งนั้น
          </div>
        </div>
      </div>

      {/* Popup Modal */}
      {showRecommendationPopup && (
        <div
          className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50"
          onClick={closePopup}
        >
          <div
            className="bg-white rounded-lg max-w-md w-full mx-4 max-h-96 overflow-y-auto"
            onClick={(e) => e.stopPropagation()}
          >
            {/* Popup Header */}
            <div className="flex items-center justify-between p-4 border-b">
              <h3 className="text-lg font-semibold text-gray-800">
                คำแนะนำสำหรับ {selectedLocation ? selectedLocation.name : "Cafe Amazon สาขา ST"}
              </h3>
              <button
                onClick={closePopup}
                className="text-gray-500 hover:text-gray-700 text-xl font-bold"
              >
                ×
              </button>
            </div>

            {/* Popup Content */}
            <div className="p-4">
              <div className="space-y-3">
                {selectedRecommendations.map((recommendation, index) => {
                  const iconPath = getRecommendationIcon(recommendation);
                  const isImageIcon = typeof iconPath === 'string' && iconPath.startsWith('/assets/images/');

                  return (
                    <div
                      key={`popup-rec-${index}`}
                      className="flex items-center gap-3 p-2 bg-gray-50 rounded-lg"
                    >
                      <span className="flex-shrink-0">
                        {isImageIcon ? (
                          <img
                            src={iconPath}
                            alt=""
                            className="w-6 h-6"
                            onError={(e) => {
                              e.target.style.display = 'none';
                              e.target.nextSibling.style.display = 'inline';
                            }}
                          />
                        ) : (
                          <span className="text-lg">{iconPath}</span>
                        )}
                        {isImageIcon && (
                          <span style={{ display: 'none' }} className="text-lg">•</span>
                        )}
                      </span>
                      <span className="flex-1 text-sm text-gray-700 leading-relaxed">
                        {recommendation}
                      </span>
                    </div>
                  );
                })}
              </div>
            </div>

            {/* Popup Footer */}
            <div className="px-4 pb-4">
              <button
                onClick={closePopup}
                className="w-full bg-green-500 hover:bg-green-600 text-white py-2 px-4 rounded-lg transition-colors"
              >
                ปิด
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

const Footer = () => (
  <footer className="h-16 flex items-center justify-between p-4 bg-green-100 border-t border-green-100 flex-shrink-0">
    <span className="text-base text-black">Made with by Jaejae Dream Yok ❤️</span>
    <div className="flex items-center gap-4">
      <div className="flex-shrink-0 flex items-center justify-center">
        <img
          src="/assets/images/logo.png"
          alt="Mupcop logo"
          className="w-14 h-14 object-contain"
          width={82}
          height={82}
        />
      </div>
      <div>
        <p className="text-xl font-bold text-black">Mupcop</p>
      </div>
    </div>
  </footer>
);

export { Header, MonitoringPanel, Footer };