import { useState, useEffect } from 'react';
import {LOCATION_CONFIGS } from '../config/firebase-configs';
import { Warning } from 'postcss';

//  Use Firebase configuration from config file and change based on location
const firebaseConfig = LOCATION_CONFIGS['cafe-amazon-st'].firebaseConfig || LOCATION_CONFIGS['building-c4'].firebaseConfig;

// Initialize Firebase (ใช้ project เดียว)
const app = initializeApp(firebaseConfig);
const database = getDatabase(app);

// Constants
const FIREBASE_USER_ID = 'gdRueJtWeNaMleXbEf4rWfuD6Kr1';
const PIERA_PATH = 'Piera/';
const RAW_PATH = 'RAWdata/';

// Helper functions - ปรับให้ใช้ local time
const getCurrentDatePath = () => {
  // ใช้ local time แทน UTC
  const date = new Date();
  const year = date.getFullYear();
  const month = (date.getMonth() + 1).toString().padStart(2, '0');
  const day = date.getDate().toString().padStart(2, '0');
  return `${year}-${month}-${day}`;
};

const getCurrentTime = () => {
  // ใช้ local time แทน UTC
  const date = new Date();
  const hours = date.getHours().toString().padStart(2, '0');
  const minutes = date.getMinutes().toString().padStart(2, '0');
  const seconds = date.getSeconds().toString().padStart(2, '0');
  return `${hours}:${minutes}:${seconds}`;
};

const formatDisplayDate = (date) => {
  // รับ Date object หรือสร้างใหม่ถ้าไม่มี
  const targetDate = date || new Date();
  const month = (targetDate.getMonth() + 1).toString().padStart(2, '0');
  const day = targetDate.getDate().toString().padStart(2, '0');
  const year = targetDate.getFullYear();
  return `${month}/${day}/${year}`;
};

// PM thresholds and color functions
export const getAirQualityColor = (status) => {
  const colors = {
    Good: '#1E88E5',
    Warningforsensitivegroups: '#2DC653',
    Warning: '#FECF3E',
    Effecthealth: '#FF9500',
    Hazardous: '#D02224'
  };
  return colors[status] || colors.Moderate;
};

export const formatPMValue = (value) => {
  const numericValue = parseFloat(value);
  if (isNaN(numericValue)) return value;
  return `${numericValue.toFixed(2)} μg/m³`;
};

export const getRecommendationIcon = (recommendation) => {

  // ตรวจสอบคำหลักในคำแนะนำและส่งคืน path ของรูปภาพที่เหมาะสม
  const text = recommendation.toLowerCase();

  if (text.includes('ไม่มีผลต่อสุขภาพ') &&
    text.includes('ใช้ชีวิตได้ตามปกติ')) {
    return '/assets/images/work.png';
  }

  // ไอคอนสำหรับการเปิดหน้าต่างระบายอากาศ
  if (text.includes('เปิดหน้าต่างระบายอากาศ')) {
    return '/assets/images/window.png';
  }

  // ไอคอนสำหรับการปิดหน้าต่าง
  if (text.includes('ปิดหน้าต่าง')) {
    return '/assets/images/window.png'; 
  }

  // ไอคอนสำหรับกลุ่มเปราะบาง/การสังเกตอาการ (รวมหลายเงื่อนไข)
  if (text.includes('สังเกตอาการของตนเอง') ||
    text.includes('กลุ่มเปราะบาง') ||
    text.includes('กลุ่มคนทั่วไป') ||
    text.includes('ความเสี่ยง')) {
    return '/assets/images/irritation.png';
  }

  // ไอคอนสำหรับการหลีกเลี่ยง/งดกิจกรรม (รวมหลายเงื่อนไข)
  if (text.includes('งดการทำกิจกรรมหรือเข้าใกล้พื้นที่ที่มีฝุ่น') ||
    text.includes('หลีกเลี่ยงการทำกิจกรรมที่ก่อให้เกิดฝุ่น')) {
    return '/assets/images/no-dust.png';
  }

  // ไอคอนสำหรับยาและอุปกรณ์การแพทย์
  if (text.includes('เตรียมยาหรืออุปกรณ์ตามคำสั่งแพทย์')) {
    return '/assets/images/medicine.png';
  }

  // ไอคอนสำหรับเครื่องฟอกอากาศ
  if (text.includes('ติดตั้งเครื่องฟอกอากาศ')) {
    return '/assets/images/air-purifier.png';
  }

  // ไอคอนสำหรับการไปพบแพทย์/โรงพยาบาล
  if (text.includes('หากมีอาการให้ไปพบแพทย์') ||
    text.includes('ไปพบแพทย์')) {
    return '/assets/images/Doctor.png';
  }

  // ไอคอนสำหรับการโหลดข้อมูล
  if (text.includes('กำลังโหลด') ||
    text.includes('กำลังวิเคราะห์') ||
    text.includes('กำลังประมวลผล')) {
    return '⏳';
  }

  // ไอคอนเริ่มต้น
  return '•';
};

export const PM_THRESHOLDS = {
  PM01: {
    Good: 0.0,
   'Warning for sensitive groups': 0.5,
    Warning: 1.0,
    'Effect on health': 2.0,
    Hazardous: Number.MAX_VALUE
  },
  PM25: {
    Good: 15.0,
    'Warning for sensitive groups': 25.0,
    Warning: 37.5,
    'Effect on health': 75.0,
    Hazardous: Number.MAX_VALUE
  },
  PM100: {
    Good: 50.0,
    'Warning for sensitive groups': 80.0,
    moderate: 120.0,
    'Effect on health': 180.0,
    Hazardous: Number.MAX_VALUE
  }
};

const determineAirQuality = (pm01, pm25, pm100) => {
  // Check Excellent
  if (pm01 <= PM_THRESHOLDS.PM01.Good &&
    pm25 <= PM_THRESHOLDS.PM25.Good &&
    pm100 <= PM_THRESHOLDS.PM100.Good) {
    return "Good";
  }
  // Check Good
  else if (pm01 <= PM_THRESHOLDS.PM01.Warningforsensitive &&
    pm25 <= PM_THRESHOLDS.PM25.Warningforsensitive &&
    pm100 <= PM_THRESHOLDS.PM100.Warningforsensitive) {
    return "Warning for sensitive";
  }
  // Check Moderate
  else if (pm01 <= PM_THRESHOLDS.PM01.Warning &&
    pm25 <= PM_THRESHOLDS.PM25.Warning &&
    pm100 <= PM_THRESHOLDS.PM100.Warning) {
    return "Warning";
  }
    // Check Effecthealth
  else if (pm01 <= PM_THRESHOLDS.PM01.Effecthealth &&
    pm25 <= PM_THRESHOLDS.PM25.Effecthealth &&
    pm100 <= PM_THRESHOLDS.PM100.Effecthealth) {
    return "Effect on health";
  }
  // If above all thresholds, return Hazardous
  else {
    return "Hazardous";
  }
};

