const getRecommendations = (airQualityStatus) => {
  // คำแนะนำเดียวกันสำหรับทุกตำแหน่ง - ใช้เกณฑ์มาตรฐานสากล
  switch (airQualityStatus) {
    case 'Good':
      return [
        "ไม่มีผลต่อสุขภาพสามารถใช้ชีวิตได้ตามปกติ",
        "สามารถเปิดหน้าต่างระบายอากาศได้"
      ];
    case 'Warning for sensitive groups':
      return [
        "ไม่มีผลต่อสุขภาพสามารถใช้ชีวิตได้ตามปกติ",
        "กลุ่มเปราะบางอาจจะมีความเสี่ยงและสังเกตอาการของตนเอง",
        "สามารถเปิดหน้าต่างระบายอากาศได้"
      ];
    case 'Warning':
      return [
        "มีความเสี่ยงต่อร่างกายทั้งกลุ่มคนทั่วไปและกลุ่มเปราะบาง",
        "หลีกเลี่ยงการทำกิจกรรมที่ก่อให้เกิดฝุ่น",
        "สังเกตอาการของตนเองหากมีอาการให้ไปพบแพทย์"
      ];
    case 'Effect on health':
      return [
        "มีความเสี่ยงต่อร่างกายทั้งกลุ่มคนทั่วไปและกลุ่มเปราะบาง",
        "หลีกเลี่ยงการทำกิจกรรมที่ก่อให้เกิดฝุ่น",
        "หากมีอาการควรเตรียมยาหรืออุปกรณ์ตามคำสั่งแพทย์",
        "ติดตั้งเครื่องฟอกอากาศ"
      ];
    case 'Hazardous':
      return [
        "มีความเสี่ยงต่อร่างกายทั้งกลุ่มคนทั่วไปและกลุ่มเปราะบาง",
        "งดการทำกิจกรรมหรือเข้าใกล้พื้นที่ที่มีฝุ่น",
        "หากมีอาการควรเตรียมยาหรืออุปกรณ์ตามคำสั่งแพทย์",
        "ติดตั้งเครื่องฟอกอากาศ"
      ];
    default:
      return [
        "กำลังโหลดข้อมูล...",
        "กำลังวิเคราะห์คุณภาพอากาศ...",
        "กำลังประมวลผลคำแนะนำ..."
      ];
  }
};